@font-face {
    font-family: 'RobotoRegular';
    font-weight: normal; 
    font-style: normal;

    src: url('../fonts/Roboto-Regular.eot');
    src: url('../fonts/Roboto-Regular.ttf') format('truetype'), url('../fonts/Roboto-Regular.woff') format('woff'), url('../fonts/Roboto-Regular.woff2') format('woff2');
}

@font-face {
    font-family: 'RobotoMedium';
    font-weight: normal; 
    font-style: normal;

    src: url('../fonts/Roboto-Medium.eot');
    src: url('../fonts/Roboto-Medium.ttf') format('truetype'), url('../fonts/Roboto-Medium.woff') format('woff'), url('../fonts/Roboto-Medium.woff2') format('woff2');
}

@font-face {
    font-family: 'RobotoBold';
    font-weight: normal; 
    font-style: normal;

    src: url('../fonts/Roboto-Bold.eot');
    src: url('../fonts/Roboto-Bold.ttf') format('truetype'), url('../fonts/Roboto-Bold.woff') format('woff'), url('../fonts/Roboto-Bold.woff2') format('woff2');
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0; 

    outline: none;
}

html {
    font-size: 46.875%;
}

img {
    width: 100%;
    max-width: 100%;
}

a:hover {
    text-decoration: none;
}

@media (min-width: 768px) {
    html {
        font-size: 54.6875%;
    }
}

@media (min-width: 992px) {
    html {
        font-size: 62.5%;
    }
}

header {
    background: #f2f3f8;
}

.gridContainer {
    max-width: 1170px;
    margin: 0 auto;
}

.headLine {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    padding: 4rem 0; 

    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

.phones {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.phones .block {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
        flex-direction: column; 

    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
}
.phones .left {
    padding-right: 1rem;

    border-right: 1px solid #ccc;
}
.phones .right {
    padding-left: 1rem;
}
.phones__number {
    padding-left: 20px;

    font-family: 'Neucha', cursive;
    font-size: 1.5rem;
    line-height: 2rem;

    color: #252525;
}
.phones__number:hover {
    color: #252525;
}

.spriteNumber {
    background-image: url('../img/css_sprites.png');
    background-repeat: no-repeat;
}

.landline {
    background-position: -5px 0;
}

.lifecell {
    background-position: -5px -33px;
}

.vodafone {
    background-position: -5px -50px;
}

.kyivstar {
    background-position: -5px -15px;
}

.logo {
    position: absolute;
    right: calc(50% - 78.5px);
}

.basketAndLang {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.basketAndLang button {
    width: 4rem;
    height: 4rem;

    border: 2px solid #e6e6e6; 
    background-color: inherit;
}
.basketAndLang button:hover {
    cursor: pointer;
}

.basket {
    margin-right: 4.1rem;
    padding-left: 38px;

    background-image: url('../img/bag.png');
    background-repeat: no-repeat;
}
.basket__name {
    margin: 0; 

    font-family: 'neucha', cursive;
    font-size: 18px;
    font-weight: 600;

    color: #252525;
}
.basket__amount {
    margin: 0; 

    font-family: 'Roboto', sans-serif;
    font-size: 12px;

    color: gray;
}

.navigation {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    font-family: 'Neucha', cursive;
    font-size: 15px;
}
.navigation .dropDown {
    margin: 0;

    color: #fff;
    background-color: #98b37d;
    background-image: url('../img/catalog.png');
    background-repeat: no-repeat;
    background-position: 25px 48%; 

    cursor: pointer;
    text-transform: uppercase;
}
.navigation__item {
    display: block;

    width: 20%;
    padding: 2.4rem 0;

    color: #252525; 
    background: #fff;

    text-align: center;
}

main {
    height: 20rem;

    background: #f2f3f8;
}

footer {
    padding-top: 25px; 

    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    background: #f2f3f8;
}

.infoLine {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.infoLine .title {
    margin-bottom: 0; 

    font-family: 'Roboto', sans-serif;
    font-size: 1.2rem;

    color: gray;
}
.infoLine ul {
    margin: 0;
}
.infoLine li {
    margin-top: 2.1rem; 

    list-style-type: none;

    font-family: 'Neucha', cursive;
    font-size: 1.5rem;
    font-weight: 600;
}
.infoLine li a {
    color: #252525;

    text-transform: uppercase;
}

.catBlocks {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.catBlocks .second {
    margin-left: 4rem;
}

.contBlocks {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.contBlocks a {
    padding-left: 17px;
}
.contBlocks .second {
    padding-left: 3.5rem;
}
.contBlocks .spriteAdress {
    padding-left: 16px;

    background-image: url('../img/adress.png');
    background-repeat: no-repeat;
}
.contBlocks .mail {
    background-position: -10px -4px;
}
.contBlocks .adress {
    background-position: -10px -37px;
}

.hrefLine {
    margin: 0 auto;
    padding: 3rem 0;

    border-bottom: 1px solid #e6e6e6;
}
.hrefLine ul {
    margin: 0;

    text-align: center;
}
.hrefLine li {
    display: inline-block; 

    margin: 0 15px;
}
.hrefLine .spriteHref {
    display: block;

    background-image: url('../img/href.png');
    background-repeat: no-repeat;
}
.hrefLine .vk {
    width: 22px;
    height: 20px;

    background-position: -209px -6px;
}
.hrefLine .ok {
    width: 15px;
    height: 21px;

    background-position: -10px -10px;
}
.hrefLine .face {
    width: 22px;
    height: 22px;

    background-position: -167px -10px;
}
.hrefLine .inst {
    width: 20px;
    height: 20px;

    background-position: -45px -10px;
}
.hrefLine .ln {
    width: 22px;
    height: 22px;

    background-position: -125px -10px;
}
.hrefLine .twit {
    width: 22px;
    height: 18px;

    background-position: -251px -10px;
}
.hrefLine .pint {
    width: 20px;
    height: 20px;

    background-position: -85px -10px;
}

.lastLine {
    padding: 2rem 0;

    font-family: 'Roboto', sans-serif;
    font-size: 12pt;

    color: #b3b3b3;

    text-align: center;
}
